* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  min-height: 100%;
  color: #DDD;
  background-color: #101010;
  font-variant: small-caps;
}

:root {
  --important: #e2ceae;
  --fontBlank: #ddd;
  --linkHover: rgb(235, 219, 190);
  --bgDownload: rgb(133, 79, 18);
  --bgCase: rgba(46, 43, 39, 0.6);
  --caseBorder: rgba(100, 100, 100, 0.6);
  --bgCaseHover: rgba(59, 53, 46, 0.6);
  --linkHover2: rgb(180, 240, 225);
  --bgRealItem: rgb(133, 79, 18);
}

