.links {
  position: absolute;
  display: flex;
  padding-top: 30px;
  right: 5%;
}

.linksNavbar, .linksNavbar2 {
  display: flex;
  justify-content: space-between;
}

.links>ul, .Menu>ul, .linksNavbar>ul, .linksNavbar2>ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  justify-content: center;
}

.links>ul>li, .Menu>ul>li {
  margin: 20px;
  padding: 3px;
  text-align: center;
  font-size: 35px;
  cursor: pointer;
  transition: all 0.25s;
}

.linksNavbar>ul>li, .linksNavbar2>ul>li, .titleNavbar {
  margin: 10px;
  padding: 3px;
  text-align: center;
  font-size: 22px;
  cursor: pointer;
}

.titleNavbar {
  pointer-events: none;
}

.links>ul>li>a {
  border-bottom: 3px solid transparent;
  transition: all 0.25s;
}

.linksNavbar>ul>li>a, .linksNavbar2>ul>li>a, .caseTitleContact>p {
  border-bottom: 1px solid transparent;
  transition: all 0.25s;
}

.links>ul>li>a:hover, .linksNavbar>ul>li>a:hover, .realLinks>span>a:hover {
  border-color: var(--linkHover);
}

.linksNavbar2>ul>li>a:hover {
  border-color: var(--linkHover2);
  color: var(--linkHover2);
}

.first {
  position: relative;
  display: flex;
  width: 100%;
  height: 100vh;
  /*
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url('./components/images/1.png');
  */
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url('./components/images/70.jpg');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

