.download {
  position: absolute;
  display: flex;
  bottom: 10%;
  width: fit-content;
  padding: 10px;
  border: 1px solid var(--linkHover);
  border-radius: 20px;
  color: #DDD;
  font-size: 45px;
  cursor: pointer;
  transition: all 0.2s;
}

@media screen and (min-width: 800px) {
  .download {
    right: 5%;
  }
}

@media screen and (max-width: 800px) and (min-width: 450px) {
  .download {
    position: absolute;
    display: flex;
    right: 5%;
    width: fit-content;
    padding: 10px;
    margin-top: 10%;
    border: 1px solid var(--linkHover);
    border-radius: 20px;
    color: #DDD;
    font-size: 31px;
    cursor: pointer;
    transition: all 0.2s;
  }
}

@media screen and (max-height: 620px) {
  .download {
    margin-top: 0%;
  }
}

