    align-self: center;
    width: fit-content;
    padding: 10px;
    margin-top: 0px;
    border: 1px solid var(--linkHover);
    border-radius: 20px;
    color: #DDD;
    font-size: 31px;
    cursor: pointer;
    transition: all 0.2s;
  }
}

@media screen and (max-width: 350px) {
  .download {
    margin-left: auto;
    margin-right: auto;
    width: fit-content;
    padding: 10px;
    margin-top: 100px;
