.gallery {
    background-color: black;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 0px;
    margin: 0px;
}

.item-gallery-layer {
    width: 33%;
    height: 30vw;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    animation: decreaseSize forwards 0.4s;
    transition: all 0.2s;
}

.item-gallery-layer:hover {
    animation: inscreaseSize forwards 0.4s;
}

@keyframes inscreaseSize {
    from {
        background-size: 100%
    }

    to {
        background-size: 110%
    }
  }

  @keyframes decreaseSize {
    from {
        background-size: 110%
    }

    to {
        background-size: 100%
    }
  }

.item-gallery-link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.2s;
    width: 100%;
    height: 100%;
    z-index: 5;
    filter: inherit;
}

.item-gallery-link:hover {
    /* --hover-color setup in Gallery.js */
    box-shadow: inset 0px 0px 50px -20px var(--hover-color);
}

@media screen and (max-width: 600px) {
    .item-gallery-link {
        width: 50%;
        height: 50vw;
    }
}

@media screen and (max-width: 400px) {
    .item-gallery-link {
        width: 100%;
        height: 100vw;
    }
}


.deleteme {
    color: #9fd9f0;
}

.item-gallery {
    opacity: 0%;
    transition: all 0.2s;
    text-align: center;
}

.item-gallery:hover {
    opacity: 100%;
}

.item-gallery-title {
    /* --title-color setup in Gallery.js */
    color: var(--title-color);
    opacity: 0%;
    position: absolute;
    text-align: center;
    transition: all 0.2s;
}

.item-gallery-link:hover > .item-gallery-title {
    opacity: 100%;
}

.item-gallery-link {
    cursor: pointer;
}