.doc
{
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  color: rgb(255, 255, 255);
  background-color: rgb(37, 37, 37);
  min-height: 100vh;
  display: flex;
  margin: 0px;
  text-align: center;
  flex-direction: row;
  font-variant: normal;
  min-width: 100%;
}

.doc span
{
  font-size: inherit;
}

::-webkit-scrollbar {
  display: none;
}

.hide-scrollbar
{
    overflow: auto;
    -ms-overflow-style: none; /* IE 11 */
    scrollbar-width: none; /* Firefox 64 */
}

.liDoc
{
  display: flex;
  flex-direction: column;
  margin: 25px 40px 25px 40px;
  border-bottom: rgba(255, 255, 255, 0.3) solid 1px;
  padding: 20px 0px 20px 0px;
}

.Section, .selectedSection
{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center; 
  flex: 1;
}

.Menu
{
  background-image: url('./components/img/HST.PNG');
  background-size:cover;
  background-position-x: 0%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  position: fixed;
  bottom: 0%;
  top: 0%;
  flex-flow: column nowrap;
  min-width: 255px;
  min-height: 100vh;
  scrollbar-width: none;
}

.scroll
{
  overflow-y: scroll;
  height: 100%;
}

.TitleMenu 
{
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 25px 20px 50px 20px;
  font-size: 25px;
  padding: 10px 0px 10px 0px;
  border-bottom: rgba(255, 255, 255, 0.3) solid 1px;
  background-color: rgba(0,0,0,0);
  transition: background-color 0.4s, box-shadow 1s;
}

.hidden
{
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition: opacity 0.4s;
}

.subMenu:hover
{
  background-color: rgba(255, 255, 255, 0.1);
}

.Page
{
  display: flex;
  flex-direction: column;
  flex-grow: 2;
  align-items: center;
  font-size: 20px;
  margin-left: 355px;
  margin-right: 100px;
}

.Section, .selectedSection
{
  margin-top: 20px;
  padding: 15px;
}

.selectedSection {
  margin-bottom: 20px;
}

.TitlePage
{
  margin-top: 40px;
  margin-bottom: 30px;
  border-bottom: rgba(255, 255, 255, 0.3) solid 1px;
}

.Docu
{
  color: #df8600;
  margin: 7px;
  font-size: 21px;
  margin-bottom: 30px;
}

.Section
{
  background-color: rgba(0,0,0,0);
  transition: background-color 0.4s, box-shadow 1s;
}

.Section:hover, .TitleMenu:hover,  .SubMenuBundle:hover
{
  background-color: #a8650052;
  transition: background-color 0.3s, box-shadow 1s;
}

.selectedSection 
{
  background-color: #a86500;
  box-shadow: 0 0px 35px -10px #bb5400;
  border: rgba(143, 76, 0, 0.993) solid 1px;
  border-radius: 3px;
  transition: background-color 0.4s, box-shadow 1s;
}

.selectedSection:hover
{
  background-color: #a86500;
}

.Page > div
{
  display:flex;
  flex-direction: column;
  align-items: center;
}

.Quote
{
  color: rgb(236, 229, 209);
  background-color: rgb(10, 10, 10);
  margin-top: 10px;
  margin-bottom: 50px;
  padding-left: 10px;
  padding-right: 10px;
}

.DocuSection , .conseils 
{
  border-bottom: rgba(255, 255, 255, 0.3) solid 1px;
  margin-bottom: 30px;
}

.Exemple
{
  border-top: rgba(255, 255, 255, 0.3) solid 1px;
  padding-top: 40px;
}

.Jules
{
  margin-top: 20px;
}

.Info
{
  font-size: 15px;
  font-style: italic;
  color: #9b9b9b;
}

.conseils
{
  padding-bottom: 30px;
}

.arrow
{
  margin-left: 20px;
}

.Code
{
  font-size: 14px;
  margin-bottom: 30px;
}

.container
{
  margin-bottom: 30px;
}

.subMenu
{
  border-bottom: rgba(255, 255, 255, 0.0) solid 1px;
  font-size: 16px;
  margin: 0px;
  padding-top: 7px;
  padding-bottom: 7px;
}

.selectedSub 
{
  background-color: rgba(255,255,255,0.2);
}

.selectedSub:hover 
{
  background-color: rgba(255, 255, 255, 0.2);
}

.Separator
{
  border-top: rgba(255, 255, 255, 0.3) solid 1px;
  display: flex;
  width: 500px;
  margin-bottom: 40px;
}

.SectionBundle
{
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  border-bottom: rgba(255, 255, 255, 0.3) solid 1px;
  margin-bottom: 25px;
}

.SubMenuBundle
{
  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 28px;
  font-size: 24px;
  padding: 10px;
  border: rgba(255, 255, 255, 0.3) solid 1px;
}

.selectedBundleSection
{
  background-color: #a86500;
  border: rgba(143, 76, 0, 0.993) solid 1px;
  border-radius: 3px;
  transition: background-color 0.4s, box-shadow 1s;
}

.selectedBundleSection:hover
{
  background-color: #a86500;
}

.QuoteConseil
{
  color: rgb(236, 229, 209);
  background-color: rgb(10, 10, 10);
  margin-top: 10px;
  margin-bottom: 8px;
  padding-left: 10px;
  padding-right: 10px;
}

.AllSubMenus
{
  opacity: 1;
  transition: opacity 0.4s;
}

